import React from 'react';
import Header, {DispatchPropsType, MapStatePropsType} from "./Header";
import {connect} from "react-redux";
import {logout} from "../../redux/auth-reducer";
import {AppStateType} from "../../redux/redux-store";


class HeaderContainer extends React.Component<MapStatePropsType & DispatchPropsType> {

    render() {
        return <Header {...this.props}/>
    }
}

const mapStateToProps = (state: AppStateType) => ({
    isAuth: state.auth.isAuth,
    login: state.auth.login
})

// @ts-ignore
export default connect<MapStatePropsType, DispatchPropsType, {}, AppStateType>(
    mapStateToProps, {logout})(HeaderContainer) //as React.ComponentType